<template>
  <div class="contenedor-radial-bars" :id="contenedor_id">
    <div :id="tooltip_id" class="tultip">
      <span class="highlight title">{{ laboratorio }} </span><br />
      <span v-if="variable_graficada == 'piezas'">
        NÃºmero de piezas:
        <span class="highlight">{{ piezas_adquiridas }}</span></span
      >
      <span v-if="variable_graficada == 'monto'">
        Monto total: <span class="highlight">{{ monto_total }}</span>
      </span>
    </div>
    <svg
      class="radial-bars"
      :id="grafica_id"
      :width="ancho - margenes.derecha - margenes.izquierda"
      :height="alto_svg"
    >
      <g
        class="grupo-barras"
        :transform="`translate(${ancho / 2 - margenes.izquierda},${
          alto / 2 + margenes.arriba - 10
        })`"
      ></g>
      <g class="eje-x"></g>
      <g
        class="eje-y"
        :transform="`translate(${ancho / 2 - margenes.izquierda},${
          alto / 2 + margenes.arriba - 10
        })`"
      ></g>
    </svg>
  </div>
</template>
<script setup>
import { ref, toRefs, onMounted } from "vue";
import * as d3 from "d3";

// eslint-disable-next-line
const props = defineProps({
  data: {
    type: Array,
    default: Array,
  },
  contenedor_id: {
    type: String,
    default: String,
  },
  grafica_id: {
    type: String,
    default: String,
  },
  variable_graficada: {
    type: String,
    default: String,
  },
  /*   titulo_grafica: {
    type: String,
    default: String,
  }, */
  tooltip_id: {
    type: String,
    default: String,
  },
});
const { data } = toRefs(props);
const { contenedor_id } = toRefs(props);
const { grafica_id } = toRefs(props);
const { variable_graficada } = toRefs(props);
//const { titulo_grafica } = toRefs(props);
const { tooltip_id } = toRefs(props);
const contenedor_svg = ref();
const svg = ref();
const grupo_barras = ref();

const alto = ref();
const alto_svg = ref();
const ancho = ref();
const margenes = ref({
  derecha: 5,
  izquierda: 5,
  abajo: 10,
  arriba: 10,
});

const innerRadius = ref(3);
const outerRadius = ref();
const escala_X = ref();
const escala_Y = ref();
const eje_x = ref();
const eje_y = ref();
const scale_font = ref();

const tooltipPetalos = ref();
const laboratorio = ref();
const piezas_adquiridas = ref();
const monto_total = ref();
const format = ref();

onMounted(() => {
  contenedor_svg.value = document.getElementById(`${contenedor_id.value}`);
  svg.value = d3.select(`svg#${grafica_id.value}`);
  grupo_barras.value = svg.value.select("g.grupo-barras");
  eje_x.value = svg.value.select("g.eje-x");
  eje_y.value = svg.value.select("g.eje-y");

  tooltipPetalos.value = d3.select(`div#${tooltip_id.value}`);
  tooltipPetalos.value.style("visibility", "hidden");
  format.value = d3.format(",.2f");

  //console.log(data.value);
  //console.log(variable_graficada.value);
  dimensionando();
  dibujandoEjes();
  dibujanding();
  window.addEventListener("resize", reescalanding);
});

function reescalanding() {
  dimensionando();
  dibujandoEjes();
  dibujanding();
}

function dimensionando() {
  // Ajustar las dimensiones
  if (window.innerWidth > 769) {
    margenes.value.derecha = 20;
    margenes.value.izquierda = 20;
    scale_font.value = 14;
  } else {
    margenes.value.derecha = 0;
    margenes.value.izquierda = 0;
    scale_font.value = 11;
  }
  ancho.value =
    contenedor_svg.value.clientWidth -
    margenes.value.derecha -
    margenes.value.izquierda;

  if (window.innerWidth > 769) {
    outerRadius.value = ancho.value * (3 / 7);
    alto_svg.value = 2 * outerRadius.value + 10;
  } else {
    outerRadius.value = ancho.value * (3 / 6.09);
    alto_svg.value = 2 * outerRadius.value + 10;
  }

  alto.value =
    alto_svg.value - margenes.value.arriba - margenes.value.abajo + 30;

  escala_X.value = d3
    .scaleBand()
    .range([0, 2 * Math.PI]) // X axis goes from 0 to 2pi = all around the circle. If I stop at 1Pi, it will be around a half circle
    .domain(data.value.map((d) => d.laboratorio)); // The domain of the X axis is the list of states.

  escala_Y.value = d3
    .scaleRadial()
    .range([innerRadius.value, outerRadius.value]) // Domain will be define later.
    .domain([0, d3.max(data.value.map((d) => d[variable_graficada.value]))]); // Domain of Y is from 0 to the max seen in the data
}
function dibujandoEjes() {
  eje_y.value.selectAll("text").remove();
  eje_y.value.selectAll("g").remove();

  eje_y.value.call((g) =>
    g
      .selectAll("g")
      .data(escala_Y.value.ticks(7))
      .join("g")
      .attr("fill", "none")
      .call((g) =>
        g
          .append("circle")
          .attr("stroke", "black")
          .attr("stroke-opacity", 0.2)
          .attr("r", escala_Y.value)
      )
      .call((g) =>
        g
          .append("text")
          .attr("y", (d) => -escala_Y.value(d))
          .attr("dy", "0.35em")
          .attr("fill", "black")
          .attr("stroke", "black")
          .attr("stroke-width", 0.2)
          .attr("text-anchor", "middle")
          .text(escala_Y.value.tickFormat(1, ",f"))
          .style("font-size", scale_font.value)
      )
  );
}
function dibujanding() {
  grupo_barras.value.selectAll("path.linea").remove();

  grupo_barras.value
    .selectAll("path.linea")
    .data(data.value)
    .enter()
    .append("path")
    .attr("class", "linea")
    .attr("stroke", (d) => d.color)
    .attr("stroke-width", 2)
    .attr("fill", (d) => d.color)
    .attr("opacity", 0.8)
    .attr("d", (d) => {
      let x =
        Math.cos(escala_X.value(d.laboratorio)) *
        escala_Y.value(d[variable_graficada.value]);
      let y =
        Math.sin(escala_X.value(d.laboratorio)) *
        escala_Y.value(d[variable_graficada.value]);
      //console.log(d.laboratorio, x, y);
      let a = 17; // Este es el ancho mÃ¡ximo de nuestro pÃ©talo
      let angl = Math.PI / 2 - escala_X.value(d.laboratorio);
      let x1 = x - a * Math.cos(angl);
      let y1 = y + a * Math.sin(angl);
      let x2 = x + a * Math.cos(angl);
      let y2 = y - a * Math.sin(angl);

      // Vamos a dibujar una curva de bÃ©zier cuadrÃ¡tica que vaya del origen al punto x,y y regrese al origen
      // Tomando como punto de control dos puntos a a de distancia sobre la perpendicular al radio correspondiente a cada pÃ©talo
      return `M0 0Q${x1} ${y1}, ${x} ${y}Q${x2} ${y2}, 0 0`;
    })
    .on("mouseover", abrirTooltip)
    .on("mouseleave", cerrarTooltip)
    .on("mousemove", ajustarPosicionTooltip);
}

function abrirTooltip() {
  let petalo_seleccionado = d3.select(this).datum();
  console.log(petalo_seleccionado);
  laboratorio.value = petalo_seleccionado.laboratorio;
  piezas_adquiridas.value = format.value(petalo_seleccionado.piezas);
  monto_total.value = format.value(petalo_seleccionado.monto);

  tooltipPetalos.value.style("visibility", "visible");
}

function cerrarTooltip() {
  tooltipPetalos.value.style("visibility", "hidden");
}

function ajustarPosicionTooltip() {
  //console.log(d3.pointer(event, document.body));
  let position = d3.pointer(event, document.body);
  let x = position[0] - 100;
  let y = position[1] + 35;

  tooltipPetalos.value.style("left", x + "px").style("top", y + "px");
}
</script>
<style>
