  display: inline;
  padding: 3px;
  height: 12px;
  width: 12px;
  margin: 2px;
}

.tultip {
  position: absolute;
  z-index: 2;
  background-color: #252323;
  color: white;
  opacity: 0.93;
  height: auto;
  width: 200px;
